import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { HttpClientModule } from '@angular/common/http'; 

import { MyApp } from './app.component';
import { AuthProvider } from '../providers/auth/auth';
import { PhotoProvider } from '../providers/photo/photo';
import { Keyboard } from '@ionic-native/keyboard';
import { Camera } from '@ionic-native/camera';
import { Base64 } from '@ionic-native/base64';
import { ImagePicker } from '@ionic-native/image-picker';

@NgModule({
  declarations: [MyApp],
  imports: [BrowserModule, IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/about-us/about-us.module.ngfactory#AboutUsPageModuleNgFactory', name: 'AboutUsPage', segment: 'about-us', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/auth/forgot-password/forgot-password.module.ngfactory#ForgotPasswordPageModuleNgFactory', name: 'ForgotPasswordPage', segment: 'forgot-password', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/auth/login/login.module.ngfactory#LoginPageModuleNgFactory', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/auth/sign-up/sign-up.module.ngfactory#SignUpPageModuleNgFactory', name: 'SignUpPage', segment: 'sign-up', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module.ngfactory#HomePageModuleNgFactory', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/question-correct-answer/question-correct-answer.module.ngfactory#QuestionCorrectAnswerPageModuleNgFactory', name: 'QuestionCorrectAnswerPage', segment: 'question-correct-answer', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/question-add-answer/question-add-answer.module.ngfactory#QuestionAddAnswerPageModuleNgFactory', name: 'QuestionAddAnswerPage', segment: 'question-add-answer', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/settings/settings.module.ngfactory#SettingsPageModuleNgFactory', name: 'SettingsPage', segment: 'settings', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/question-list/question-list.module.ngfactory#QuestionListPageModuleNgFactory', name: 'QuestionListPage', segment: 'question-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/statistics/statistics.module.ngfactory#StatisticsPageModuleNgFactory', name: 'StatisticsPage', segment: 'statistics', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/students/students.module.ngfactory#StudentsPageModuleNgFactory', name: 'StudentsPage', segment: 'students', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/upload-image/upload-image.module.ngfactory#UploadImagePageModuleNgFactory', name: 'UploadImagePage', segment: 'upload-image', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/question-details/question-details.module.ngfactory#QuestionDetailsPageModuleNgFactory', name: 'QuestionDetailsPage', segment: 'question-details', priority: 'low', defaultHistory: [] }
  ]
}),HttpClientModule],
  bootstrap: [IonicApp],
  entryComponents: [MyApp],
  providers: [
    StatusBar,
    SplashScreen,
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    AuthProvider,
    PhotoProvider,
    Camera,
    Base64,
    ImagePicker,
    Keyboard
  ]
})
export class AppModule {}



